@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Raleway', sans-serif;
}

body {
  margin: 0;
  background-color: #f7f1f1;
}


.todos {
  background-color: #ff6666;
  width: 500px;
  height: 100%;
  min-height: 600px;
  margin: 40px auto;
  padding: 2rem 0 3rem;
  position: relative;
  color: #fff;
}

.todos::after {
  content: "";
  width: 100%;
  height: 100%;
  background-color: #a9a6a67a;
  position: absolute;
  top: -25px;
  left: -21px;
  z-index: -1;
  border-radius: 5px;
}

.todos h1,
form h1 {
  font-weight: bold;
  font-size: 40px;
  margin: 0;
}

.todos h3,
form h3 {
  margin: 0;
  font-weight: lighter;
  border-bottom: 1px solid;
  padding-bottom: 10px;
}

.container {
  margin-left: 40px;
  margin-right: 40px;
}

.todo {
  background-color: rgba(255, 255, 255, 0.1);
  height: 100%;
  max-height: 90px;
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.todo .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  grid-gap: 7px;
  gap: 7px;
}

.todo .container h2 {
  margin: 0;
  font-weight: 400;
  font-size: 27px;
}

.todo .container p {
  margin: 0;
  font-weight: lighter;
  font-size: 19px;
}

.todo input {
  margin-right: 6px;
  width: 25px;
  height: 20px;
  border: none;
  cursor: pointer;
}

.todo svg {
  font-size: 22px;
  margin: 0 15px 2px 0;
  cursor: pointer;
  transition: color 0.8s;
  color: rgba(255, 255, 255, 0.1);
}

.todo svg:hover {
  color: white;
}

.todo.done {
  text-decoration: line-through;
}

.todo.done svg {
  color: #fff;
}

.new-todo {
  background-color: #ff6666;
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 12px;
  text-align: left;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  color: #fff;
}

